@import "../../custom.scss";

.info {
  .contactText {
    @media (max-width: 600px) {
      text-align: center;
    }
    h2 {
      font-size: 24px;
      font-weight: 700;
    }
    p {
      width: 80%;
      line-height: 30px;
      font-size: 14px;
      opacity: 0.8;
      @media (max-width: 600px) {
        width: 100%;
      }
    }
  }
  .contactInfo {
    p {
      font-size: 18px;
      font-weight: 700;
      span {
        opacity: 0.82;
      }
    }
  }
}
